.bg-img {
  background-image: linear-gradient(
      to bottom,
      hsla(0, 0%, 32%, 0.6),
      hsla(213, 49%, 47%, 0.3)
    ),
    url(../../images/backgroundimg.jpeg) !important;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  /* -webkit-filter: grayscale(80%); */
  margin: 0px;
  display: block;
  /* overflow-y: scroll; */
  width: 100%;
  min-height: 100% !important;
  color: white;
}

